<template>
  <div id="votes">
    <div v-if="error">
      {{ error }}
    </div>
    <table class="table">
        <thead>
            <tr>
                <th>Titel</th>
                <th>M책ned</th>
                <th>Dato</th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="board in boards" :key="board.id">
                <td>{{ board.title }}</td>
                <td>{{ board.month }}</td>
                <td>{{ board.date }}</td>
            </tr>
        </tbody>
    </table>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: 'boards',
  data () {
    return {
      boards: [],
      error: null
    }
  },
  async mounted () {
    try {
      const response = await axios.get('http://localhost:8947/boards')
      this.boards = response.data
    } catch (error) {
      this.error = error;
    }
  }
}
</script>
